.hero {
    height: 100vh;
    background: linear-gradient(180deg, #FDE1FF, #E1FFEA22 60%);
    display: flex;
}

.hero-left {
    /* border: 3px solid tomato; */
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* gap: 20px; */
    gap: 1.25rem;
    padding-left: 180px;
    line-height: 1.1;
}

.hero-left h2 {
    color: #090909;
    font-size: 26px;
    font-weight: 600;
}

.hero-left p {
    color: #171717;
    font-size: 4.5rem;
    font-weight: 700;
}

.hero-hand-icon {
    display: flex;
    align-items: center;
    gap: 20px;
}
.hero-hand-icon img {
    /* border: 2px solid blue; */
    width: 105px;
}
.hero-latest-button {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 15px;
    width: 310px;
    height: 70px;
    border-radius: 75px;
    margin-top: 30px;
    background-color: #ff4141;
    font-size: 22px;
    color: white;
    font-weight: 500;
}

.hero-right {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

/* --- Responsiveness --- */
@media(max-width:1440px) { 
    .hero-left {
        /* border: 2px solid red ; */
        padding-left: 100px; 
    }
    .hero-left h2 {
        font-size: 22px;
    }
    .hero-left p {
        font-size: 60px;
    }
    .hero-hand-icon img {
        width: 80px;
    }
    .hero-latest-button {
        gap: 10px;
        width: 250px;
        height: 60px;
        margin-top: 20px;
        font-size: 18px;
    }
    .hero-right img {
        /* width: 500px; */
        width: 450px;
    }
}
@media(max-width:1024px) { 
    .hero-left {
        padding-left: 80px;
    }
    .hero-left h2 {
        font-size: 20px;
    }
    .hero-left p {
        font-size: 50px;
    }
    .hero-hand-icon img {
        width: 65px;
    }
    .hero-latest-button {
        width: 220px;
        height: 50px;
        font-size: 16px;
    }
    .hero-right img {
        width: 400px;
    }
}
@media(max-width:800px) { 
    .hero-left {
        padding-left: 30px;
    }
    .hero-left h2 {
        font-size: 16px;
    }
    .hero-left p {
        font-size: 40px;
    }
    .hero-hand-icon img {
        width: 50px;
    }
    .hero-latest-button {
        width: 275px;
        height: 40px;
        font-size: 13px;
    }
    .hero-right img {
        width: 300px;
    }
}
@media(max-width:500px){
    .hero{
        flex-direction: column;
    }
    .hero-right {
        display: none;
    }
    .hero-left h2 {
        font-size: 18px;
    }
    .hero-left p {
        font-size: 50px;
    }
    .hero-hand-icon img {
        width: 55px;
    }
    .hero-latest-button {
        width: 205px;
        height: 45px;
        font-size: 14px;
    }
}